import { Button, Icon } from '@adobe/elsie/components';
import { useText } from '@adobe/elsie/i18n';
import { Close } from '@adobe/elsie/icons';
import { VComponent, classes } from '@adobe/elsie/lib';
import { FunctionComponent, VNode } from 'preact';
import { HTMLAttributes } from 'preact/compat';

import '@adobe/elsie/components/InLineAlert/InLineAlert.css';
export interface InLineAlertProps
  extends Omit<HTMLAttributes<HTMLDivElement>, 'icon'> {
  heading: string;
  description?: string;
  type?: 'error' | 'warning' | 'success';
  icon?: VNode<HTMLAttributes<SVGSVGElement>>;
  additionalActions?: Array<{
    label: string;
    onClick: (event: Event) => void;
  }>;
  onDismiss?: (event: Event) => void;
}

export const InLineAlert: FunctionComponent<InLineAlertProps> = ({
  className,
  type = 'warning',
  additionalActions,
  onDismiss,
  heading,
  description,
  icon,
  ...props
}) => {
  const translations = useText({
    dismiss: 'Dropin.InlineAlert.dismissLabel',
  });

  return (
    <div
      {...props}
      className={classes([
        'dropin-in-line-alert',
        `dropin-in-line-alert--${type}`,
        className,
      ])}
    >
      <div className={'dropin-in-line-alert__heading'}>
        <div className={'dropin-in-line-alert__title-container'}>
          {icon && (
            <VComponent node={icon} className={'dropin-in-line-alert__icon'} />
          )}

          <span className={'dropin-in-line-alert__title'}>{heading}</span>
        </div>

        <div className={'dropin-in-line-alert__actions-container'}>
          {additionalActions?.length == 1 && (
            <Button
              variant="tertiary"
              className={'dropin-in-line-alert__additional-action'}
              onClick={additionalActions[0]?.onClick}
              aria-label={additionalActions[0]?.label}
            >
              {additionalActions[0]?.label}
            </Button>
          )}
          {onDismiss && (
            <Button
              icon={<Icon source={Close} size="24" stroke="2" />}
              className={'dropin-in-line-alert__dismiss-button'}
              variant="tertiary"
              onClick={onDismiss}
              aria-label={translations.dismiss}
            />
          )}
        </div>
      </div>

      {description && (
        <p className={'dropin-in-line-alert__description'}>{description}</p>
      )}

      {additionalActions && additionalActions?.length > 1 && (
        <div className={'dropin-in-line-alert__additional-actions-container'}>
          {additionalActions.map((action) => (
            <Button
              key={action.label}
              variant="tertiary"
              className={'dropin-in-line-alert__additional-action'}
              onClick={action.onClick}
            >
              {action.label}
            </Button>
          ))}
        </div>
      )}
    </div>
  );
};
